import { useMemo, useState } from 'react';
import './Login.css';

import axios from "axios";
import { useNavigate } from 'react-router-dom';

function Login() {

    //         csrf-

    // npm run build 
    
    axios.defaults.xsrfCookieName = 'csrftoken';
    axios.defaults.xsrfHeaderName = 'X-CSRFToken';

    const navigate = useNavigate();

    //   URL  dev-  
    const basicURL = process.env.NODE_ENV === 'development' ? 'http://localhost:8000' : ''

    useMemo(() => {
        //      /check-user/,   check-user/
        //       login/check-user/
        axios.get(basicURL + "/check-user/", 

            {
                headers: {
                'Content-Type': 'application/json',
                }

            }
            ).then(response => {
                
                if(response.data.user.username !== 'AnonymousUser'){
                    navigate('/');
                }

            }).catch((error) => {
            console.log("error", error)

            alert(': ' + error);

            });

    }, [navigate]);

    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');

    const usernameHandler = (event) => {
        
        setUsername(event.target.value);

    }

    const passwordHandler = (event) => {
        
        setPassword(event.target.value);

    }

    const loginSubmitHandler = (event) => {
        
        event.preventDefault();

        if (username.length === 0) {

            alert(':  ');
            return;
        }

        if (password.length === 0) {

            alert(':  ');
            return;
        }

        //     .   POST-  json-    {},  headers    data
        
        axios.post(basicURL + "/login-user/", 
            {
                'username': username,
                'password': password
            },

            {
                headers: {
                'Content-Type': 'application/json',
                }

            }
            ).then(response => {

                navigate('/');

            }).catch((error) => {
            console.log("error", error.response.data.error_message)
                
            alert(': ' + error.response.data.error_message);

            });


    }

    return (<div className="my_container">
		<div className="login-form">
			<div className="form-header">
				<text>    </text>
			</div>

			<form onSubmit={loginSubmitHandler}>
                <input type="text" name="username" placeholder="" value={username} onChange={usernameHandler} />

                <input type="password" name="password" placeholder="" value={password} onChange={passwordHandler} />

				<input type="button" name="submitButton" value="  " onClick={loginSubmitHandler}/>

			</form>
		</div>
	</div>
);
}


export default Login;